import { render } from 'lit-html';
import { FlashCardTemplate } from '../views/flashcard/FlashCardTemplate';

/* useState */
let state = {
  selectedCard: 0,
  slidePosition: '0vw',
  cards: [],
  cxt: '',
  isTabKey: false,
  loading: true
};
function setState (newState) {
  state = { ...state, ...newState };
  render(FlashCardTemplate(), document.body);
}
function getState () {
  return state;
}

export { setState, getState, state };
