/* Start themeStyles - styles supplied by the configuration side panel
 ** CSS Inline & CSS URL
 ** this code:
 ** attachThemeStyle() attachLocalStyle():
 ** createInlineCSS({ styles: string, styleId: string }): creates a style block in the header & injects any customcss from user *** dangerous *** */

const attachThemeStyle = ({ customCss = '', id = 'global' }) => {
  return createInlineCSS({ styles: customCss, style_Id: id });
};
const attachLocalStyle = ({ customCss = '', id = 'flashcards' }) => {
  return createInlineCSS({ styles: customCss, styleId: id });
};

export const createInlineCSS = ({ styles, styleId }) => {
  const { css = '', cssUrl = '' } = styles;
  const styleSheetContent = cssUrl ? `${css} @import url('${cssUrl}')` : css;
  const styleTagID = styleId;

  let styleEl = document.getElementById(styleTagID) || false;
  if (styleEl && styleSheetContent === styleEl.innerHTML) return;
  const head = document.head || document.getElementsByTagName('head')[0];

  if (!styleEl) {
    /* incase bundler has removed <style> rebuild */
    styleEl = document.createElement('style');
    styleEl.id = styleTagID;
    head.appendChild(styleEl);
  }
  /* NOTE: empty any pre-existing css styles before appending */
  styleEl.innerHTML = '';
  styleEl.appendChild(document.createTextNode(styleSheetContent));
  return styleEl;
};

/*  */
function getRgba (rgba, alpha = 1) {
  if (!rgba) return '';

  const lftBr = rgba.indexOf('(') + 1;
  const rgtBr = rgba.indexOf(')');
  const rgbArr = rgba.substring(lftBr, rgtBr).split(',');
  const r = rgbArr[0];
  const g = rgbArr[1];
  const b = rgbArr[2];

  return `rgba(${r},${g},${b},${alpha})` || rgba;
}

const setThemeCss = ({
  bodyFont = 'sans-serif',
  titleFont = 'sans-serif',
  primaryColor = 'rgba(46,159,255,1)'
}) => {
  const el = document.documentElement.style;

  el.setProperty('--body-font', bodyFont);
  el.setProperty('--title-font', titleFont);
  el.setProperty('--primary-color', primaryColor);
  el.setProperty('--primary-color-pulse-start', getRgba(primaryColor, 0.6));
  el.setProperty('--primary-color-pulse', getRgba(primaryColor, 0));
  el.setProperty('--primary-color-hover', getRgba(primaryColor, 0.6));
  el.setProperty('--primary-color-inactive', getRgba(primaryColor, 0.4));
};

export { setThemeCss, attachThemeStyle, attachLocalStyle };
