import CDP from '@pearsontechnology/aero-plugin-api/lib/cdp';
import { getState } from './state-management';
const userInteraction = new Set();
let userModified = false;

export const userModificationLogger = ({ cardsTotal, cardId }) => {
  userInteraction.add(cardId);
  userModified = true;
  
  const numberModified = userInteraction.size;
  const allModified = numberModified === cardsTotal;
  
  // Keep CDP User Interaction state logged 
  try {
    if (getState().cxt?.toUpperCase() === 'LEARNER') {
       CDP.state('allModified').set( allModified );
       CDP.state('userModified').set( userModified );
       CDP.state('numberModified').set( numberModified );
    }
  } catch {
    console.info('CDP not engaged');
  }

}




