import { html } from 'lit-html';
import { chevron } from '../../images/chevron';
import { setState, getState } from '../../utils/state-management';

import { arrowShift, selectStepperCard } from './stepper-utils';

const StepperNav = () => {
  const { cards: flashcards } = getState();
  const state = getState();
  return html`
    <ol class="card-container__dots-wrapper">
      <li class="card-container__dot control left">
        <div
          aria-role="button"
          data-index-number=${state.selectedCard - 1}
          @keydown=${event => {
            if (event.code === 'ArrowLeft') arrowShift(-1);
          }}
          @click=${() => arrowShift(-1)}
        >
          <div class="chevron left" alt="previous flash card">
            ${chevron}
          </div>
        </div>
      </li>

      <li>
        <div class="dots-container__wrapper">
          ${flashcards.map((_, dot) => {
            const dotClass = () =>
              parseInt(dot) === parseInt(state.selectedCard) ? ' active' : '';
            return html`
              <div
                class="card-container__dot ${dotClass()}"
                data-index-number=${dot}
                @click=${event => {
                  setState({ selectedCard: dot });
                  selectStepperCard();
                }}
                aria-controls="flashcard-${dot}"
                role="presentation"
                data-check=${state.selectedCard}
              ></div>
            `;
          })}
        </div>
      </li>

      <li class="card-container__dot control right">
        <div
          role="button"
          data-index-number=${state.selectedCard + 1}
          @keydown=${event => {
            if (event.code === 'ArrowRight') arrowShift(1);
          }}
          @click=${() => arrowShift(1)}
        >
          <div class="chevron right" alt="next flash card">
            ${chevron}
          </div>
        </div>
      </li>
    </ol>
  `;
};
export default StepperNav;
