import { html } from 'lit-html';

const Instructions = (CARDCOUNT) => {
return html`
<section tabindex="0" 
  aria-describedby="dialogDesc" 
  id="instructions" class="flashcard__instructions">
  <h1 id="dialogTitle"> Instructions: How to Navigate Flash Cards</h1>
  <p id="dialogDesc"> 
    <p>The total amount flash cards to navigate will be ${CARDCOUNT}</p> 
    <p>Use Tab enter flash cards.</p>
    <p>Use Right or Left arrows to navigate to the next or previous available flash card.</p> 
    <p>Use Space-bar or the Enter key to toggle the card to show back or front of cards.</p>
    <p>Use Tab leave flash cards.</p> 
  </p>
</section>
`
}

export default Instructions;
