import CDP from '@pearsontechnology/aero-plugin-api/lib/cdp';

import { setThemeCss, attachThemeStyle, attachLocalStyle } from './utils';
import { setState } from './utils/state-management';

/** This is your entry point and the logic that takes care of connecting your schema.config properties */
window.__CDP = CDP;
function plugin () {
  CDP.register().then(() => {

    CDP.state()
      .get()
      .then(config => {
        setState(config);
      });
    CDP.state().subscribe(config => {
      setState(config);
    });

    CDP.state('style').subscribe(cssStyles => {
      const { cssUrl, cssInline: css } = cssStyles;
      const customStyleCss = { css, cssUrl };
      attachLocalStyle({ customCss: customStyleCss });
    });

    try {
      CDP.context().get().then( cxt => { 
        setState({ cxt: cxt });
      });
    } catch {
       // by pass 
      console.info("CDP context not allocated");
    }; 

    CDP.theme()
      .get()
      .then(themeCSS => setState(themeCSS));
    CDP.theme().subscribe(themeCSS => {
      const { bodyFont, titleFont, primaryColor, css, cssUrl } = themeCSS;
      setThemeCss({ bodyFont, titleFont, primaryColor });

      const customCssTheme = { css, cssUrl };
      attachThemeStyle({ customCss: customCssTheme });
    });
  });
}

document.addEventListener('DOMContentLoaded', plugin);
