import { html } from 'lit-html';
import { unsafeHTML } from 'lit-html/directives/unsafe-html.js';

import { setState, state, getState } from '../../utils/state-management';
import { userModificationLogger } from '../../utils/user-interaction-logger';

import { handleKeypressed } from '../../utils/accessibility-keyboard';

import Instructions from './Instructions';
import StepperNav from '../navigation/StepperNav';
import StepperNavCompact from '../navigation/StepperNavCompact';

const gridLayout = cardtotal => {
  const numberOfColumns = Math.ceil(Math.sqrt(cardtotal));
  const numberOfRows = Math.ceil(cardtotal / numberOfColumns);
  /* Sets the Grid Container Dimensions & behaviour */
  return `
    --gridTemplateColumns: ${numberOfColumns};
    --gridTemplateRows: ${numberOfRows};
  `;
};

const stepperLayout = cardtotal => {
  return `
    --stepper-repeat: ${cardtotal};
    --stepper-slide-panel-width: ${100 * cardtotal}vw;
  `;
};

const shuffleCards = cardsArray => {
  return [...cardsArray].sort(() => Math.random() - 0.5);
};

const imageClass = displaySelectionType => {
  const fitStyle = displaySelectionType?.toUpperCase();
  const bgClass = {
    CONTAIN: 'contain',
    COVER: 'cover',
    FILL: 'fill'
  };
  return bgClass[fitStyle] || 'contain';
};

export const FlashCardTemplate = () => {
  console.info('-v2.0.12 FlashCard config: ', state);

  const { cards = [], objectFit, randomize, stepper, navBar, activityTitle,activityLabel,credit,captions, instructions} = state;
  const CARDCOUNT = cards.length;
  const cardStyle = stepper ? 'stepper' : '';
  const flashcards = randomize && cards ? shuffleCards([...cards]) : cards;
  const getCardCount = () => CARDCOUNT;
  const layoutStyle = 
    stepper && getCardCount()
      ? stepperLayout(getCardCount())
      : gridLayout(getCardCount());

  const miniNavType =
    navBar?.toUpperCase() === 'EXPANDED' ? StepperNav() : StepperNavCompact();

  const upDateUserModification = () => {
    const activeCard = document.querySelectorAll('.card-container')[getState().selectedCard];
    const cardsides = activeCard.querySelectorAll('section');

    const front = cardsides[0];
    const back = cardsides[1];
    const flipped = activeCard.classList.contains('active');

    if (!flipped) {
      activeCard.setAttribute('aria-pressed', 'true');
      back.setAttribute('aria-hidden', 'false');
      front.focus({
        preventScroll: true
      });
      front.setAttribute('aria-hidden', 'true');
    } else {
      activeCard.setAttribute('aria-pressed', 'false');
      back.focus({
        preventScroll: true
      });
      back.setAttribute('aria-hidden', 'true');
      front.setAttribute('aria-hidden', 'false');
    }
    userModificationLogger({cardsTotal:getCardCount(), cardId: state.selectedCard});
  }

    /* display widget template: */
  return html`

  ${ Instructions(CARDCOUNT) }
  <div class="component-wrapper">

      ${(activityTitle || activityLabel) && html`<h4 class="component-label" >${activityLabel && html`${unsafeHTML(activityLabel)}&nbsp;`}<span class="component-title">${unsafeHTML(activityTitle??'')}</span></h4>`}
  ${ instructions ? 
          html`<div class="component-instruction">
            ${unsafeHTML(instructions)}
            </div>`:
          ''
  }    
  
  <div class=${`wrapper ${cardStyle} `} style='${layoutStyle}' >
  
      <div class=${`cards-wrapper ${cardStyle}`} >
      
        <ol  
          class=${`cards-container ${cardStyle}`} 
          style=${`transform:translate3d(${state.slidePosition || 0},0,0)`} >
        ${flashcards &&
          flashcards.map((card, indNo) => {
            const dataIndex = indNo;
            const frontImageBgColor = card['front-background-color']?.length
              ? `${card['front-background-color']}`
              : '';
            const backImageBgColor = card['back-background-color']?.length
              ? `${card['back-background-color']}`
              : '';
            const frontImageStyle =
              card && card['front-image']?.length
                ? `background: ${frontImageBgColor} url(${card['front-image']}) no-repeat; `
                : `background: ${frontImageBgColor}`;
            const backImageStyle =
              card && card['back-image']?.length
                ? `background: ${backImageBgColor} url(${card['back-image']}) no-repeat; `
                : `background: ${backImageBgColor}`;

            return html`
              <li
                class="card"
                @ontouchstart=${event =>
                  event.currentTarget.classList.toggle('active')}
              >
                <div
                  class="card-container"
                  id="flashCard${indNo + 1}"
                  role="button"
                  tabindex="0"
                  @focus=${event => setState({ selectedCard: indNo })}
                  @click=${event => {
                    event.currentTarget.classList.toggle('active');
                    setState({ selectedCard: indNo });
                    upDateUserModification();
                  }}
                  @keydown=${event =>
                    handleKeypressed({
                      keyEvent: event,
                      cardCount: getCardCount(),
                      state: state,
                      stepper: stepper
                    })}
                  data-index-number=${dataIndex}
                >
                  <section
                    class=${`front ${imageClass(objectFit)}`}
                    id="front-card-${indNo + 1}"
                    aria-hidden="false"
                    style="${frontImageStyle}"
                  >
                    ${card['front-image-altText']?.length > 0
                      ? html`
                          <span
                            role="img"
                            aria-label=${card['front-image-altText']}
                          />
                        `
                      : html``}
                    <div class="inner">
                      ${unsafeHTML(card['front-text'])}
                    </div>
                  </section>

                  <section
                    class=${`back ${imageClass(objectFit)}`}
                    style=${backImageStyle}
                    aria-hidden="true"
                    id="back-card-${indNo + 1}"
                  >
                    ${card['back-image-altText']?.length > 0
                      ? html`
                          <span
                            role="img"
                            aria-label=${card['back-image-altText']}
                          />
                        `
                      : html``}
                    <div class="inner">
                      ${unsafeHTML(card['back-text'])}
                    </div>
                  </section>
                </div>
              </li>
            `;
          })}
        </ol>

      </div>
    
      
     ${
       stepper
         ? html`
             <navigation class="navigation-mininav-wrapper"  aria-hidden="true">
               ${miniNavType}
             </navigation>
           `
         : ''
     }
      </div>
      ${captions && html`<div class="component-caption">${unsafeHTML(captions)}</div>`}
      ${credit && html`<div class="component-credit">${unsafeHTML(credit)}</div>`}
  </div>

  `;

};
