import { html } from 'lit-html';
import { chevronStd } from '../../images/chevron-std';
import { getState } from '../../utils/state-management';
import { arrowShift } from './stepper-utils';

const StepperNavCompact = () => {
  const state = getState();
  const totalOfCards = getState().cards.length;

  return html`
    <div class="container">
      <div class="controls">
        <div class="left directional-btn"
            aria-role="button"
            data-index-number=${state.selectedCard - 1}
            @keydown=${event => {
              if (event.code === 'ArrowLeft') arrowShift(-1);
            }}
            @click=${() => arrowShift(-1)}
            ><i>${chevronStd}</i>
        </div>

          <div class="page" aria-role="presentation">
            <span class="current" data-current="${state.selectedCard}"
              >${state.selectedCard + 1}</span
            >
            &nbsp;of&nbsp;
            <span class="total" data-total="${totalOfCards}"
              >${totalOfCards}</span
            >
          </div>
        
        <div class="right directional-btn"
            aria-role="button"
            data-index-number=${state.selectedCard + 1}
            @click=${() => arrowShift(1)}
            @keydown=${event => {
              if (event.code === 'ArrowRight') arrowShift(1);
            }}
            ><i>${chevronStd}</i>
        </div>
      </div>
    </div>
  `;
};

export default StepperNavCompact;
