import { setState, getState } from './state-management';

const getAllCardContainers = () => document.querySelectorAll('.card-container');
export const getValidCard = ({ cardNo = 0, cardCount }) => {
  return cardNo < 0 ? cardCount - 1 : cardNo > cardCount - 1 ? 0 : cardNo;
}

const updateAccessibilityAttributes = ({ cardItems }) => {
  [...cardItems].forEach((card, i) => {
    const show = i === getState().selectedCard;
 
    if (show) {
      card.tabIndex = '0';
      card.focus({
        preventScroll: true
      });
    } else {
      card.tabIndex = '-1';
      card.blur();
    }
  });
};

const flipCard = () => {
  const activeCard = getAllCardContainers()[getState().selectedCard];
  activeCard.click();
};

const startCardCycle = () => {
  /* init card cycle for stepper */
  setState({ selectedCard: 0 });
  updateAccessibilityAttributes({ cardItems: getAllCardContainers() });
};

const handleKeypressed = ({ keyEvent, cardCount, state, stepper }) => {
  const CAPTURE_KEYS = [
    'Space',
    'Enter',
    'ArrowRight',
    'ArrowLeft',
    'ArrowUp',
    'ArrowDown'
  ].includes(keyEvent.code);
  const MOVE_KEYS = [
    'ArrowRight',
    'ArrowLeft',
    'ArrowUp',
    'ArrowDown'
  ].includes(keyEvent.code);
  const SELECT_KEY = ['Space', 'Enter'].includes(keyEvent.code);
  
  setState({ isTabKey: ['Tab'].includes(keyEvent.code)});

  if (!CAPTURE_KEYS) return;

  if (SELECT_KEY || MOVE_KEYS) keyEvent.preventDefault();

  /* Left Right movement with Left, Right,Tab arrow keys */
  if (MOVE_KEYS) {
    const step =
      keyEvent.shiftKey || ['ArrowLeft', 'ArrowUp'].includes(keyEvent.code)
        ? -1
        : 1;

    const newCardNo = getValidCard({
      cardNo: parseInt( getState().selectedCard ) + step,
      cardCount: cardCount
    });

    if (stepper) {
     
      if ( getState().loading ) {
        startCardCycle();
        setState({ loading: false });
      }
      const slidePosition = newCardNo !== 0 ? `${100 * newCardNo * -1}vw` : '0';
      setState({ selectedCard: newCardNo, slidePosition: slidePosition });
      updateAccessibilityAttributes({ cardItems: getAllCardContainers() });
    } else {
      setState({ selectedCard: newCardNo });
      updateAccessibilityAttributes({ cardItems: getAllCardContainers() });
    }
  }
  SELECT_KEY && flipCard();
};

export { handleKeypressed, startCardCycle };
