import { getValidCard } from '../../utils/accessibility-keyboard';
import { setState, getState } from '../../utils/state-management';

const handleStepperKeypress = (event, fn) => {
  event.preventDefault();
  const KEY = event.code;
  const ACTION_KEYS = ['ArrowRight', 'ArrowLeft'].includes(event.code);
  if (!ACTION_KEYS) return;

  const step = KEY === 'ArrowRight' ? 1 : -1;
  const newCard = getValidCard({
    cardNo: getState().selectedCard + step,
    cardCount: getState().cards.length
  });
  setState({ selectedCard: newCard });
};

const arrowShift = step => {
  // @ts-ignore
  const newCardNo = getValidCard({
    cardNo: parseInt(getState().selectedCard) + step,
    cardCount: getState().cards?.length
  });
  setState({ selectedCard: newCardNo });
  selectStepperCard();
};
const selectStepperCard = () => {
  // @ts-ignore
  const newCardNo = parseInt(getState().selectedCard);
  const slidePosition = newCardNo !== 0 ? `${100 * newCardNo * -1}vw` : '0vw';
  setState({ slidePosition: slidePosition });
};

export { arrowShift, handleStepperKeypress, selectStepperCard };
